import React, { useEffect, useState } from 'react'
import sanityClient from '../client'
import { useParams } from 'react-router-dom'
import imageUrlBuilder from '@sanity/image-url'
import BlockContent from '@sanity/block-content-to-react'

const builder = imageUrlBuilder(sanityClient)
function urlFor(source){
    return builder.image(source)
}

function SinglePost() {

    const [singlePost,setSinglePost] = useState(null)
    const { slug } = useParams()

    // crook

    console.log(slug)

    useEffect(() =>{
        sanityClient.fetch(`*[slug.current == "${slug}"]{
            title,
            _id,
            slug,
            mainImage{
                asset->{
                    id,
                    url
                }
            },
            body,
            "name": author->name,
            "authorImage": author->image
        }`)
        .then((data) => setSinglePost(data[0]))
        .catch((err) => console.log(err))
    },[])

    if(!singlePost){
        return (<div>
            Loading...
        </div>)
    }

    return (
        <main className="bg-gray-200 min-h-screen p-12">
            <article className="container mx-auto bg-blue-100 rounded-lg">
                <header className="relative">
                    <div className="absolute h-full w-full flex items-center justify-center p-8">
                        <div className="bg-white bg-opacity-75 rounded p-12">
                            <h1 className="text-3xl lg: text-6xl mb-4">{singlePost.title}</h1>
                            <div className="flex justify-center text-gray-800">
                                <img src={urlFor(singlePost.authorImage).url()} alt={singlePost.name}  className="w-10 h-10 rounded-full"/>
                                <p className="flex items-center pl-2 text-2xl">{singlePost.name}</p>    
                            </div>
                        </div>
                    </div>
                    <img src={singlePost.mainImage.asset.url} alt={singlePost.title} className="w-full object-cover rounded-top"
                    style={{ height: "400px" }}
                    />
                </header>
                <div className="px-6 lg:px-48 py-py-12 lg:py-20 prose lg:prose-xl max-w-full">
                    <BlockContent blocks={singlePost.body} projectId="p6xszvg1" dataset="production" />
                </div>
            </article>
        </main>
    )
}

export default SinglePost
